import Vue from 'vue'
import VueRouter from 'vue-router'
import AboutMe from "../views/AboutMe";
import Play from "../views/Play";
import Me from "../views/Me";

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    component:AboutMe
  },
  {
    path: '/play',
    component: Play
  },
  {
    path: '/me',
    component: Me
  }

]

const router = new VueRouter({
  routes
})

export default router
